.security-header {
    padding: .75rem 1.25rem;
    height: 3rem;
    margin-bottom: 0;
    background-color: #f0ffff;
    border-bottom: 1px solid rgba(0, 0, 0, .125); 
}    

.bar_table td{
    border-top: 1px solid black;
    position: relative;
    padding: .70rem;
    white-space: nowrap;
}

.bar_table div{
    width: 500%;
}

.bar_table {
    border-collapse: collapse;
    width: 100%;
}

.bar {
    position: relative;
    background:  #B1E7F4; 
    text-align: right;
}

.nas_graph_table th {
    border: 1px solid #000000;
